<template>
  <div id="zptime">
    {{setDate}}
  </div>
</template>

<script>
export default {
  name: 'zptime',
  data() {
    return {
      date: new Date(),
    }
  },
  computed: {
    setDate() {
      return `: ${this.date.getFullYear()}${this.date.getMonth() + 1}${this.date.getDate()}-${this.date.getHours()}${this.date.getMinutes()}${this.date.getSeconds()}`
    }
  },
  mounted() {
    setInterval(() => { this.date = new Date() }, 1000);
  },

}
</script>
<style scoped>
#zptime {
  line-height: 75px;
  color: rgba(255, 255, 255, 0.7);
  font-size: 20px;
}
</style>
