export function setForeignData(foreignList) {
  let confirmData = [];
  let nowConfirmData = [];
  let foreignData = {};
  for (let value of foreignList) {
    confirmData.push({
      name: value.name,
      value: value.confirm
    })
    nowConfirmData.push({
      name: value.name,
      value: value.nowConfirm
    })
  }
  foreignData = {
    confirmData,
    nowConfirmData
  }
  return foreignData
}